import api from '../../api/api'
import MyHeader from '../../components/header/header'
import storage from '../../common/storage'

export default {
	data () {
		return {
			userInfo: {}
		}
	},
  methods: {
  	/*
  	* 获取用户信息
  	*/
  	_getUserInfo () {
  		api.getUserInfo()
  		.then((res) => {
  			if(res.status === 'ok'){
  				this.userInfo = res.data
  				storage.s_setItem('userInfo',res.data)
  			}else if(res.status === 'error'){
  				console.log(res.error)
  				this.promptFun({
  					content: res.data,
  					type: 'error'
  				})
  			}
  		})
  	},
  	/*
  	* 上传图片
  	* 单张
  	*/
  	_upImg (e) {
  		api.upImg(e)
  		.then((res) => {
  			if(res.status === 'ok'){
  				this.userInfo.header_img = res.data[0]
  			}else if(res.status === 'error'){
  				this.promptFun({
  					content: res.data,
  					type: 'error'
  				})
  			}
  		})
  	},
  	/*
  	* 修改个人信息
  	*/
  	_editUserInfo () {
  		api.editUserInfo({
  			username: this.userInfo.username,
  			header_img: this.userInfo.header_img,
  			sex: this.userInfo.sex,
  			signature: this.userInfo.signature
  		})
  		.then((res) => {
  			if(res.status === 'ok'){
  				this.promptBackFun({
  					content: '修改成功',
  					type: 'success'
  				})
  			}else if(res.status === 'error'){
  				this.promptFun({
  					content: res.data,
  					type: 'error'
  				})
  			}
  		})
  	}
  },
  created () {
  	this._getUserInfo();
  },
  components: {
  	MyHeader
  }
}



