import api from '../../../api/live'
export default {
	data () {
		return {
			liveClassList: []
		}
	},
  methods: {
  	// 获取直播分类
  	_getLiveClass () {
  		api.getLiveClass()
  		.then((res) => {
  			if(res.status === 'ok'){
  				this.liveClassList = res.data
  			}else if(res.status === 'error'){
  				this.promptFun({
  					content: res.data,
  					type: 'error'
  				})
  			}
  		})
  	}
  },
  created () {
  	this._getLiveClass()
  }
}



