import api from '../../api/api'
import MyHeader from '../../components/header/header'
import AddAddress from '../../components/addAddress/addAddress'

export default {
	data () {
		return {
			addressList: [],
			addressModelState: false,
			item:{}
		}
	},
  methods: {
  	/*
  	* 获取地址
  	*/
  	_getMyAddress () {
  		api.getMyAddress().then((res) => {
  			if(res.status === 'ok'){
  				this.addressList = res.data
  			}else if(res.status === 'error'){
  				this.promptFun({
            content: res.data,
            type: 'error'
          })
  			}
  		})
  	},
  	/*
  	* 设置默认／删除地址
  	*/
  	_setAddress (id,type) {
  		api.setAddress(id,type).then((res) => {
  			console.log(res)
  			if(res.status === 'ok'){
  				this._getMyAddress()
          this.promptFun({
            content: res.data,
            type: 'success'
          })
  			}else if(res.status === 'error'){
  				this.promptFun({
            content: res.data,
            type: 'error'
          })
  			}
  		})
  	},
  	/*
  	* 新增地址mdel
  	* state=true(打开)
  	* state=false(关闭)
  	*/
  	modelToogle (state,i) {
  		this.addressModelState = state
  		this.item = i || undefined
  	}
  },
  created () {
  	this._getMyAddress()
  },
  components: {
  	AddAddress,
  	MyHeader
  }
}



