import storage from '../../../common/storage'

export default {
	props: {
		userInfo: {
			type: Object,
			default: function () {//默认值
        return {}
    	}
		}
	},
  methods: {
  	/*
		* 订单列表跳转
		*/
  	orderTypeClick (state) {
  		storage.s_setItem("orderListState",state)
  		this.$router.push({
        path:'/orderList'
      })
  	}
  }
}



