import api from '../../../api/api'

export default {
	props: {
		state: {
			type: String,
			default: null
		},
		orderInfo: {
			type: Object,
			default () {
				return {}
			}
		},
		onRefresh: {
			type: Function,
			default: undefined
		}
	},
	data () {
		return {
			orderData: {}
		}
	},
  methods: {
  	/*
  	* 订单操作
  	*/
  	_orderOperate (id,type) {
  		api.orderOperate(id,type)
  		.then((res) => {
  			if(res.status === 'ok'){
  				if(this.onRefresh){
  					if(type == 5){
	  					history.back()
	  				}else{
  						this.onRefresh()
  					}
  				}
  				this.promptFun({
  					content: res.data,
  					type: 'success'
  				})
  			}else if(res.status === 'error'){
  				this.promptFun({
  					content: res.data,
  					type: 'error'
  				})
  			}
  		})
  	},
  	/*
  	* 继续支付
  	*/
  	_payOrderFun () {
  		api.payOrderFun(this.orderInfo.pay_no,2)
  		.then((res) => {
  			if(res.status ==='ok'){
        	pingpp.createPayment(res.data, function (result, error) {
	          if (result == "success") {
	          	this.promptFun({
	          		content:'支付成功',
		             type:'success'
	          	})
		          if(this.onRefresh){
		          	this.onRefresh()
		          }
	          } else if (result == "fail") {
	          	this.promptFun({
	          		content:'支付失败',
		             type:'error'
	          	})
	          } else if (result == "cancel") {
	          	this.promptFun({
	          		content:'支付取消',
		             type:'success'
	          	})
	          }
	        })
        }else if(res.status === 'error'){
          this.promptFun({
        		content:res.data,
            type:'error'
        	})
        }
  		})
  	}
  },
  watch: {
  	orderInfo () {
  		this.orderData = this.orderInfo
  	}
  }
}



