import api from '../../../api/api'

export default {
	props:{
    invalidInfo: {
      type: Array,
      default: function () {//默认值
        return []
    	}
    },
  },
  methods: {
  	delInvalidShopCarClick () {
  		api.delInvalidShopCar()
      .then((res) => {
  			if(res.status === 'ok'){
  				this.promptFun({
  					content: res.data,
  					type: 'success'
  				})
	        this.$emit('on-del')
  			}else if(res.status === 'error'){
  				this.promptFun({
  					content: res.data,
  					type: 'error'
  				})
  			}
  		})
  	}
  }
}



