import api from '../../../api/api'
export default {
	data () {
		return {
			shopDetailsInfo: {},
			shopTabType: 1
		}
	},
  methods: {
  	/*
  	* 获取店铺详情
  	*/
  	_getShopDetailsFun () {
  		api.getShopDetailsFun(this.$route.query.id)
  		.then((res) => {
  			if(res.status ==='ok'){
          this.shopDetailsInfo = res.data
        }else if(res.status === 'error'){
          console.log(res.data)
        }
  		})
  	},
  	/*
  	* 关注
  	*/
  	followClick () {
  		console.log("关注")
  	},
  	/*
  	* tab
  	*/
  	shopTabClick (type) {
  		this.shopTabType = type
  	},
  	rightTopBtnClick () {
  		console.log(111)
  	}
  },
  created () {
  	this._getShopDetailsFun()
  },
}



