import { mapGetters } from 'vuex'

export default {
  computed:{
    setStyle () {
      return {
        color: this.promptData.textColor || '#fff',
        background: this.promptData.backgroundColor ? this.promptData.backgroundColor : this.promptData.type === 'error' ? 'red' : '#21e7b6'
      }
    },
    ...mapGetters([
      'promptData',
      'promptState'
    ])
  }
}



