export default {
	props: ['maxNum'],
	data() {
		return {
			count:1
		}
	},
  methods: {
  	/*
  	* 加
  	*/
  	plusClick() {
  		if(this.count<=this.maxNum){
  			++this.count
  		}else{
  			alert("不能再加了")
  		}
  		console.log(this.count)
  		this.$emit('on-plus',this.count)
  	},
  	minusClick() {
  		if(this.count>1){
  			--this.count
  		}else{
  			alert("不能再减了")
  		}
  		console.log(this.count)
  		this.$emit('on-minus',this.count)
  	}
  }
}



