import api from '../../api/live'
import LiveTop from './private/liveTop'
import RoomBottom from './private/roomBottom'
import InputModel from './private/inputModel'
import LiveGoods from './private/liveGoods'
import gift from './private/gift'
export default {
	data () {
		return {
			videoSrc: null,
			playImgState: true,
			btnType: null,
		}
	},
  methods: {
  	// 播放
  	play () {
  		console.log(111)
  		this.playImgState = false
  		this.$refs.myVideo.play();
  	},
  	// model 打开事件
  	onOpen (type) {
  		console.log(type)
  		this.btnType = type
  	},
  	// model 关闭事件
  	onClose (type) {
  		console.log(type)
  		this.btnType = null
  	},
  	// 进入直播间
  	_intoLiveRoom () {
  		api.intoLiveRoom(this.$route.query.live_id)
  		.then((res) => {
  			console.log(res)
  			if(res.status === 'ok'){
  				this.promptFun({
  					content: '进入直播间',
  					type: 'success'
  				})
  			}else if(res.status === 'error'){
  				this.promptFun({
  					content: res.data,
  					type: 'error'
  				})
  			}
  		})
  	}

  },
  created () {
  	// 
  	setTimeout(() => {
  		this.$nextTick(function(){
  			this.$refs.myVideo.src = 'http://mse.tstmobile.com/Public/admin/Uploads/video/obs124574545.mp4'
			})
  	}, 20);
  },
  mounted () {

  },
  components: {
  	RoomBottom,
  	InputModel,
  	LiveGoods,
  	gift,
  	LiveTop
  }
}



