import api from '../../../api/api'
export default {
  data() {
  	return {
  		goodsClassList:[]
  	}
  },
  methods: {
  	_getGoodsClassFun() {
      api.getGoodsClassFun()
      .then((res) => {
        if(res.status ==='ok'){
          for(let i=0;i<res.data.length;i++){
            this.goodsClassList.push(res.data[i])
          }
        }else if(res.status === 'error'){
          console.log(res.error)
        }
      })
    }
  },
  created() {
  	this._getGoodsClassFun()
  }
}



